* {
	margin: 0;
	padding: 0;
}

html,
body {
	height: 100%;
}

.hero {
	overflow: hidden;
	height: 100vh;
	background: #edeae8;
	position: relative;
}

video {
	position: relative;

	/* Vertical and Horizontal center*/
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.heroText {
	color: #ffffff;
	font-size: 50px;
	text-align: center;
	margin-top: 40vh;
	font-family: 'Roboto Slab', serif;
}

.heroText2 {
	color: #ffffff;
	font-size: 14px;
	text-align: center;
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
}

.about {
	color: #c9c9c9;
	font-size: 14px;
	font-family: 'Montserrat', san-serif;
	font-weight: 400;
	-webkit-writing-mode: vertical-rl;
	        writing-mode: vertical-rl;
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	margin-top: -9vh;
	margin-left: 3vw;
}

.about:hover {
	color: #fff;
	cursor: pointer;
}

